<template>
  <button
    class="single_select"
    @click="onSelect"
    :class="{ selected_style: selected }"
  >
    <div class="hidden md:block">
      {{ option }}
    </div>
    <div class="md:hidden">
      <img
        src="@/assets/images/tick.svg"
        alt=""
        :class="{ hidden: selected }"
        class="tick"
      />
      <img
        src="@/assets/images/tick_white.svg"
        alt=""
        :class="{ hidden: !selected }"
        class="tick"
      />
    </div>
  </button>
</template>

<script>
export default {
  name: "SingleSelect",
  data() {
    return {
      selected: false
    };
  },
  props: {
    option: {
      type: String,
      default: ""
    }
  },
  methods: {
    onSelect() {
      this.selected = !this.selected;
      this.$emit("onClick", this.selected);
    }
  }
};
</script>

<style scoped>
.single_select {
  @apply rounded-full border text-sm border-customGray-superlight md:border-customGray font-titillium font-light w-10 h-10 flex flex-col justify-center items-center;
}
@media (min-width: 768px) {
  .single_select {
    min-width: 100px;
    @apply p-2;
  }
}

.selected_style {
  @apply bg-customLightBlue-dark text-white;
}
</style>
