<template>
  <div class="form">
    <div class="apply" v-if="currentScreen === 'apply'">
      <div class="main_heading">
        Apply directly
      </div>
      <div class="flow_section">
        <div class="left_section">
          <div class="section_heading">Tech skills</div>
          <div class="block">
            <div class="block_heading">JavaScript</div>
            <single-select
              option="Proficient"
              @onClick="onSelect('JavaScript')"
            />
          </div>
          <div class="block">
            <div class="block_heading">TypeScript</div>
            <single-select
              option="Proficient"
              @onClick="onSelect('TypeScript')"
            />
          </div>
          <div class="block">
            <div class="block_heading">Node.js</div>
            <single-select option="Proficient" @onClick="onSelect('Node.js')" />
          </div>
          <div class="block">
            <div class="block_heading">Vue.js</div>
            <single-select option="Proficient" @onClick="onSelect('Vue.js')" />
          </div>
          <div class="block">
            <div class="block_heading">MongoDB</div>
            <single-select option="Proficient" @onClick="onSelect('MongoDB')" />
          </div>
          <div class="block">
            <div class="block_heading">MariaDB</div>
            <single-select option="Proficient" @onClick="onSelect('MariaDB')" />
          </div>
          <div class="block">
            <div class="block_heading">Open source contributor?</div>
            <single-select
              option="Yes"
              @onClick="onSelect('Open source contributor?')"
            />
          </div>
        </div>
        <div class="right_section">
          <div class="section_heading">You</div>
          <div class="inputs">
            <div class="input_block">
              <input
                type="text"
                class="inputbox"
                id="fullname_input"
                placeholder="Full name"
                v-model="fullname"
                :class="{ error_box: errorFullname.error }"
              />
            </div>
            <div class="input_block">
              <input
                type="text"
                class="inputbox mob"
                id="mob_input"
                placeholder="Mobile"
                :value="mob"
                @input="onInputMob"
                :class="{ error_box: errorMob.error }"
              />
              <div class="plus">
                +91
              </div>
            </div>
            <div class="input_block">
              <input
                type="text"
                class="inputbox"
                id="email_input"
                placeholder="Email"
                v-model="email"
                :class="{ error_box: errorEmail.error }"
              />
            </div>
            <div class="input_block dropdown">
              <single-select-drop-down
                :options="degreeOptions"
                placeholder="Bachelor’s degree"
                @selected="onSelectDegree"
                :error="errorDegree.error"
              />
            </div>
            <div class="input_block">
              <input
                type="text"
                class="inputbox"
                id="academic_institution"
                placeholder="Academic institution"
                v-model="academic_institution"
                :class="{ error_box: errorAcademicInstitution.error }"
              />
            </div>
            <div class="input_block dropdown">
              <single-select-drop-down
                :options="yearsOptions"
                placeholder="Years of experience"
                @selected="onSelectYears"
                :error="errorYears.error"
              />
            </div>
            <div class="input_block">
              <input
                type="text"
                class="inputbox"
                id="code_repo"
                placeholder="Your code repo (GitHub, etc.)"
                v-model="code_repo"
                :class="{ error_box: errorCodeRepo.error }"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="button_section">
        <div class="not_eligible" v-if="!eligible">
          Sorry, you are not eligible to apply at this time.
        </div>
        <apply-button
          v-if="eligible"
          :active="activateApply"
          @onClick="onClickApply"
        />
      </div>
    </div>
    <div class="verify" v-if="currentScreen === 'verify'">
      <div class="content">
        <div class="verify_section">
          <div class="section_heading">
            Please verify your contact information
          </div>
          <div class="otp_section">
            <div class="otp_block">
              <div class="otp_heading">
                Mobile OTP
              </div>
              <div class="otp_input">
                <otp-input
                  :error="errorMobOtp"
                  @change="mobOtpChange"
                  @completed="mobOtpComplete"
                  :verified="mobOtpVerified"
                />
              </div>
            </div>
            <div class="otp_block">
              <div class="otp_heading">
                Email OTP
              </div>
              <div class="otp_input">
                <otp-input
                  :error="errorEmailOtp"
                  @change="emailOtpChange"
                  @completed="emailOtpComplete"
                  :verified="emailOtpVerified"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="button_section verify_button">
          <apply-button
            :active="activateVerify"
            text="Verify"
            @onClick="onClickVerify"
          />
        </div>
      </div>
    </div>
    <div class="complete" v-if="currentScreen === 'complete'">
      <div class="section_heading">
        Congratulations!
      </div>
      <div class="img">
        <img
          src="@/assets/images/tick_large_blue.svg"
          alt=""
          class="tick_blue"
        />
      </div>
      <div class="sub_text">
        We have received your application. We will get back to you as soon as
        possible.
      </div>
    </div>
  </div>
</template>

<script>
import SingleSelect from "@/components/SingleSelect";
import SingleSelectDropDown from "@/components/SingleSelectDropDown";
import ApplyButton from "@/components/ApplyButton";
import OTPInput from "@/components/OTPInput";
export default {
  name: "Form",
  data() {
    return {
      selectedList: {
        JavaScript: false,
        TypeScript: false,
        "Node.js": false,
        "Vue.js": false,
        MongoDB: false,
        MariaDB: false,
        "Open source contributor?": false
      },
      fullname: "",
      errorFullname: {
        error: false
      },
      mob: "",
      errorMob: {
        error: false
      },
      email: "",
      errorEmail: {
        error: false
      },
      academic_institution: "",
      errorAcademicInstitution: {
        error: false
      },
      code_repo: "",
      errorCodeRepo: {
        error: false
      },
      degreeOptions: ["hello"],
      yearsOptions: ["hello"],
      degreeSelected: "",
      yearsSelected: "",
      errorDegree: {
        error: false
      },
      errorYears: {
        error: false
      },
      completeForm: false,
      completeVerification: false,
      mobOtp: "",
      emailOtp: "",
      errorMobOtp: false,
      errorEmailOtp: false,
      emailOtpVerified: false,
      mobOtpVerified: false
    };
  },
  components: {
    "single-select": SingleSelect,
    "single-select-drop-down": SingleSelectDropDown,
    "apply-button": ApplyButton,
    "otp-input": OTPInput
  },
  computed: {
    currentScreen() {
      return "complete";
      // switch between apply verify and complete
      /* if (this.completeForm && this.completeVerification) {
        // set complete screen
        return "complete";
      } else if (this.completeForm) {
        // set verification screen
        return "verify";
      } else {
        return "apply";
      } */
    },
    activateVerify() {
      if (this.emailOtpVerified && this.mobOtpVerified) {
        return true;
      } else {
        return false;
      }
    },
    activateApply() {
      // check all the input validation passes
      // validate fullname
      let validateFullname = this.validateFullname();
      let validateMob = this.validateMob();
      let validateEmail = this.validateEmail();
      let validateDegree = this.validateDegree();
      let validateAcademicInstitution = this.validateAcademicInstitution();
      let validateYears = this.validateYears();
      let validateCodeRepo = this.validateCodeRepo();

      if (
        validateFullname &&
        validateMob &&
        validateEmail &&
        validateDegree &&
        validateAcademicInstitution &&
        validateYears &&
        validateCodeRepo
      ) {
        return true;
      } else {
        return false;
      }
    },
    eligible() {
      // set eligible true when Node.js, Vue.js and java script are selected
      if (
        this.selectedList["JavaScript"] &&
        this.selectedList["Node.js"] &&
        this.selectedList["Vue.js"]
      ) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    onClickVerify: function() {
      this.completeVerification = true;
    },
    onClickApply: function() {
      // on click Apply button
      this.completeForm = true;
    },
    onSelect: function(value) {
      this.selectedList[value] = !this.selectedList[value];
    },
    validateFullname: function() {
      // special character detection format
      let specialCharacterFormat = /[!@#$%^&*()_+\-=[\]{};':"\\|,.<>/?]+/;
      let digitFormat = /\d/;

      if (this.fullname === "") {
        this.errorFullname.error = true;
        return false;
      } else if (
        specialCharacterFormat.test(this.fullname) ||
        digitFormat.test(this.fullname)
      ) {
        this.errorFullname.error = true;
        return false;
      } else {
        this.errorFullname.error = false;
        return true;
      }
    },
    onInputMob: function(e) {
      //stop the input when input length reaches 10
      if (e.target.value.replace(/\D/g, "").length <= 10) {
        // strip characters
        var strippedMob = e.target.value.replace(/\D/g, "");
        // if the first value is 0, delete it
        if (strippedMob[0] === "0") {
          // slice the 0
          strippedMob = strippedMob.slice(1);
        }
        this.mob = strippedMob;
      }
      // the above equates to an empty string if the input is only characters, vue's reactivity will not update, so force update on every input
      this.$forceUpdate();
    },
    validateEmail: function() {
      // email format in regex code
      var validEmailFormat = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

      if (this.email === "") {
        this.errorEmail.error = true;
        return false;
      } else if (!validEmailFormat.test(this.email)) {
        this.errorEmail.error = true;
        return false;
      } else {
        this.errorEmail.error = false;
        return true;
      }
    },
    validateMob: function() {
      // if mobile is empty return false
      if (this.mob == "") {
        this.errorMob.error = true;
        return false;
      } else if (this.mob.length === 10) {
        this.errorMob.error = false;
        return true;
      }
    },
    validateAcademicInstitution: function() {
      // special character detection format
      let specialCharacterFormat = /[!@#$%^&*()_+\-=[\]{};':"\\|,.<>/?]+/;
      let digitFormat = /\d/;

      if (this.academic_institution === "") {
        this.errorAcademicInstitution.error = true;
        return false;
      } else if (
        specialCharacterFormat.test(this.academic_institution) ||
        digitFormat.test(this.academic_institution)
      ) {
        this.errorAcademicInstitution.error = true;
        return false;
      } else {
        this.errorAcademicInstitution.error = false;
        return true;
      }
    },
    validateCodeRepo: function() {
      // validate code repo
      if (this.code_repo == "") {
        // set error
        this.errorCodeRepo.error = true;
        return false;
      } else {
        // change error
        this.errorCodeRepo.error = false;
        return true;
      }
    },
    validateDegree: function() {
      // check whether degree is selcted
      if (this.degreeSelected !== "") {
        this.errorDegree.error = false;
        return true;
      } else {
        this.errorDegree.error = true;
        return false;
      }
    },
    validateYears: function() {
      // check whether degree is selcted
      if (this.yearsSelected !== "") {
        this.errorYears.error = false;
        return true;
      } else {
        this.errorYears.error = true;
        return false;
      }
    },

    onSelectDegree: function(value) {
      // degree selected
      this.degreeSelected = value;
    },
    onSelectYears: function(value) {
      // years selected
      this.yearsSelected = value;
    },
    emailOtpChange: function() {
      // remove error
      this.errorEmailOtp = false;
    },
    mobOtpChange: function() {
      // remove error
      this.errorMobOtp = false;
    },
    emailOtpComplete: function() {
      // verify

      this.emailOtpVerified = true;
    },
    mobOtpComplete: function() {
      console.log(true);
      this.mobOtpVerified = true;
    }
  },
  watch: {
    fullname: function() {
      this.validateFullname();
    },
    email: function() {
      this.validateEmail();
    },
    academic_institution: function() {
      this.validateAcademicInstitution();
    },
    code_repo: function() {
      this.validateCodeRepo();
    },
    mob: function() {
      this.validateMob();
    }
  }
};
</script>

