<template>
  <div class="main_section" :style="mainSectionStyle">
    <div class="main_content">
      We are disrupting the US $330 billion global contact center industry by
      creating
      <br />the
      <span class="font-semibold text-black"
        >largest distributed contact center</span
      >
      on earth that any business anywhere can afford
    </div>
    <div class="sub_content">
      We are hiring
      <span class="inline bg-red text-white px-3 sm:px-3 sm:py-1"
        >startup-oriented software engineers</span
      >
      in India. <span class="font-semibold">Apply below.</span>
    </div>
    <div class="incubated_section">
      <div class="heading">Incubated at</div>
      <img src="@/assets/images/nsrcel.svg" alt="" class="logo" />
    </div>
  </div>
</template>

<script>
export default {
  name: "MainSection",
  data() {
    return { windowHeight: window.innerHeight };
  },
  computed: {
    mainSectionStyle() {
      return { height: `${this.windowHeight}px`, width: `100%` };
    }
  },
  methods: {
    onResize() {
      this.windowHeight = window.innerHeight;
    }
  },
  mounted() {
    // on window resize
    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.onResize);
  }
};
</script>

